.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  position: fixed;
  width: 100%;
  top: 0;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}


.App-layout{
  position: fixed;
  width: 100%;
  bottom: 0;
}
.App-footer{
  position: fixed;
  width: 100%;
  bottom: 0;
}